/* @import url(//db.onlinewebfonts.com/c/70bd34e287f233c84a98d03a6d36dabf?family=BrandonGrotesqueWeb-Regular); */

html,body{margin: 0; min-width: 1280px;}
ul,li,ol{ list-style-type: none; margin: 0; padding: 0; }
h1,h2,h3,h4,figure,p{margin: 0;}
a{text-decoration: none;}
*{
    box-sizing: border-box;
    font-size: 18px; color: #333333;
    font-family: "BrandonGrotesqueWeb-Regular", sans-serif; font-style: normal; font-weight: 400;
}
@font-face {font-family: "BrandonGrotesqueWeb-Regular"; src: url("//db.onlinewebfonts.com/t/70bd34e287f233c84a98d03a6d36dabf.eot"); src: url("//db.onlinewebfonts.com/t/70bd34e287f233c84a98d03a6d36dabf.eot?#iefix") format("embedded-opentype"), url("//db.onlinewebfonts.com/t/70bd34e287f233c84a98d03a6d36dabf.woff2") format("woff2"), url("//db.onlinewebfonts.com/t/70bd34e287f233c84a98d03a6d36dabf.woff") format("woff"), url("//db.onlinewebfonts.com/t/70bd34e287f233c84a98d03a6d36dabf.ttf") format("truetype"), url("//db.onlinewebfonts.com/t/70bd34e287f233c84a98d03a6d36dabf.svg#BrandonGrotesqueWeb-Regular") format("svg"); }

body{ position: relative;}

/* 
  ==============================  App.js   ==================================== 
  =============================================================================
*/

nav{ width: 80%; height: 120px; padding-top: 40px;
  display: flex; align-items: center; justify-content: space-between;
  position: absolute; top: 0; left: 10%; z-index: 50;
}
nav a{ color: #cccccc;
  font-family: "BrandonGrotesqueWeb-Regular", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 16px;
  letter-spacing: .14em;
}
nav > a{ width: 200px;}
nav > a img{ width: 200px;}

nav div a{ padding: 0 30px;}
nav div a.active{ color: white;}
nav div a:hover{ color: white;}


/* 
  ==============================  Main.js   =================================== 
  =============================================================================
*/


.main{
  background: url('./img/bg1.jpg') no-repeat center top;
  width: 100%; min-height: 100vh; 
  background-size: cover; background-position-x: 50%;
}
.mainLuca{
  width: 100%; height: 100vh;
  position: relative;
}
.mainLuca img{ width: 35%; position: absolute; right: 10%; top: calc(50% + 120px); transform: translateY(-50%);}


/* 
  ===========================  FeatureMov.js   =================================== 
  =============================================================================
*/
.feature{
  background: url('./img/bg2.jpg') no-repeat center top;
  width: 100%; background-size: cover; background-position-x: 50%;
  padding-top: 120px; min-height: 100vh;
  overflow-x: hidden;
}


/* 검색 */

.searchBar{ width: 80%; margin: 40px auto;}
.searchBar p{ 
  width: 300px; border-bottom: 2px solid #c8c8c8;
  display: flex; align-items: center; justify-content: space-between;
}
.searchBar p span{
  width: 17px; height: 17px; display: block;
  background: url('./img/search.png') no-repeat center top;
  opacity: 0.8; background-size: cover;
  margin: 0 10px 5px 5px;
}

input[type=search]{
  width: 260px; padding: 5px 10px; background: none; border: none;
  color: #e3e3e3;   outline: none;
  border: none;
}
input[type=search]::placeholder{ color: #c8c8c8;}
input::-webkit-search-decoration,
input::-webkit-search-cancel-button,
input::-webkit-search-results-button,
input::-webkit-search-results-decoration{
	display:none;
}

/* 영화 정보 */
.long{ 
  display: flex; align-items: center; justify-content: flex-start;
  padding: 0px 0 80px 10%;
  overflow-x: scroll; -ms-overflow-style: none;
}
.long::-webkit-scrollbar{display: none;}
.long li{  width: 200px; margin-right: 30px; }
.long li:nth-of-type(1){ transform: translateY(60px);}
.long li:nth-of-type(2n){ transform: translateY(10px);}
.long li:nth-of-type(3n){ transform: translateY(30px);}
.long li:nth-of-type(4n){ transform: translateY(80px);}
.long li:nth-of-type(5n){ transform: translateY(50px);}


.long li > a{ display: block; width: 200px; height: 500px; box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.3); }
.long li > a:hover{ opacity: .7;}
.long li > a img{width: 100%; height: 100%; vertical-align: top; object-fit: cover;}

.long article{ 
  display: flex; align-items: center; justify-content: space-between;
  padding: 10px; 
}
.long article span{ color: #e3e3e3; font-size: 20px; text-shadow: 5px 5px 5px rgba(0, 0, 0, 0.8); }
.long article a{ width: 20px; height: 20px; opacity: 0.8; border-radius: 50%;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.8); transition: .2s;
}
.long article a:hover{ opacity: 0.3;}
.long article a img { width: 100%;}


/* 
  ===========================  Popup.js   ==================================== 
  =============================================================================
*/

.trailer{
  display: block;
  position: fixed; left: 0; top: 0;
  background-color: rgba(0, 0, 0, 0.8);
  width: 100%; height: 100vh;
  z-index: 50; cursor:default;
}
.trailer div{
  position: absolute; left: 50%; top: 50%;
  transform: translate(-50%, -50%);
}

/* 
  ===========================  Character.js   ===================================== 
  =============================================================================
*/

.char{
  width: 100%; background-size: cover;
  background-position: center top;
  background-position-x: 50%;
  background-repeat: no-repeat;
  padding-top: 120px; min-height: 100vh;
}
.char > div{ 
  width: 80%; margin: 0 auto; padding: 80px 0;
  display: flex; align-items: flex-start; justify-content: space-between;
}

/* 캐릭터 얼굴 */
.face {
  width: 300px; margin-top: 40px;
  display: flex; align-items: center; justify-content: flex-start; flex-wrap: wrap;
}
.face p { 
  width: 75px; height: 75px; margin: 10px; border-radius: 50%;
  box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.3);
  cursor: pointer;
}
.face p img{ 
  width: 100%; height: 100%; 
}


.ex{ width: 72%;}
.ex > p{  
  margin-bottom: 20px;
  color: white; font-size: 22px; 
  text-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);
} /* 이름 */
.ex span{ 
  display: block; margin-bottom: 60px;
  color: #c8c8c8; font-size: 16px; 
  text-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);
  max-height: 280px; overflow-y: scroll;-ms-overflow-style: none;
}
.ex span::-webkit-scrollbar{display: none;}
/* 내용 */

.ex figure{
  display: flex; align-items: center; justify-content: space-between;
  flex-wrap: wrap;
}
.ex figure p{
  width: 23%; padding: 10px 0;
}
.ex figure p:nth-of-type(5),.ex figure p:nth-of-type(6),
.ex figure p:nth-of-type(7),.ex figure p:nth-of-type(8){
  display: none;
}
.ex figure p img{
  width: 100%;
}







/* 
  ===========================  ShortMov.js   ===================================== 
  =============================================================================
*/

.short{
  background: url('./img/bg3.jpg') no-repeat center top;
  width: 100%; min-height: 100vh; padding-top: 120px;
  background-size: cover; background-position-x: 50%;
}



/* 영화 정보 */

.shortFil{ 
  display: flex; align-items: center; justify-content: flex-start;
  padding: 0px 0 80px 10%;
  overflow-x: scroll; -ms-overflow-style: none;
}
.shortFil::-webkit-scrollbar{display: none;}
.shortFil li{  width: 200px; margin-right: 30px; }
.shortFil li:nth-of-type(1){ transform: translateY(60px);}
.shortFil li:nth-of-type(3n){ transform: translateY(10px);}
.shortFil li:nth-of-type(5n){ transform: translateY(30px);}
.shortFil li:nth-of-type(2n){ transform: translateY(80px);}
.shortFil li:nth-of-type(5n){ transform: translateY(50px);}

.shortFil li a{ display: block; width: 200px; height: 500px; box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.3); }
.shortFil li a:hover{ opacity: 0.8;}
.shortFil li a img{width: 100%; height: 100%; vertical-align: top; object-fit: cover;}

.shortFil span{ padding: 10px; display:block; color: #e3e3e3; font-size: 20px; text-shadow: 5px 5px 5px rgba(0, 0, 0, 0.8); }


/* 
  ===========================  ShortPop.js   ===================================== 
  =============================================================================
*/

.shBg{
  position: fixed; left: 0; top: 0;
  background-color: rgba(0, 0, 0, 0.8);
  width: 100%; height: 100vh;
  z-index: 50; cursor:default;
}
.shortInfo{
  width: 1000px;
  background-size: cover;
  background-position: center top;
  background-position-x: 50%;
  background-repeat: no-repeat;
  position: absolute; left: 50%; top: 50%;
  transform: translate(-50%, -50%);
  position: relative;
}
.info{
  display: flex; align-items: center; justify-content: space-between;
  padding: 60px 50px;
}
.info > p{ width: 30%;}
.info > p img{ width: 100%;}

.info article{ width: 60%; }
.info article h2{ 
  color: white; font-size: 20px; 
  text-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);
}
.info article p{ 
  color: #c8c8c8; padding: 30px 0;
  text-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);
  max-height: 400px; overflow-y: scroll; -ms-overflow-style: none;
}
.info article p::-webkit-scrollbar{display: none;}

.info button{
  position: absolute; right: 25px; top: 25px;
  color: rgb(163, 163, 163); font-size: 30px; font-weight: 500;
  background: none; border: none; cursor: pointer;
}






/* 
  ===========================  About.js   ===================================== 
  =============================================================================
*/


.about{
  background: url('./img/bg4.jpg') no-repeat center top;
  width: 100%; min-height: 100vh; padding-top: 120px;
  background-size: cover; background-position-x: 50%;
}
.about > div{
  width: 80%; margin: 0 auto; margin-top: 80px; padding-bottom: 40px;
  display: flex; align-items: flex-start; justify-content: space-between;
}

/* 왼쪽 */
.left{ width: 60%;}
.left > span{
  color: white; font-size: 13px; font-weight: 500; 
  font-style: normal; letter-spacing: .14em;
}
.write {  margin-top: 70px; } 
.write li{ margin: 20px 0; }
.write li:nth-of-type(1){ display: flex; align-items: flex-end; justify-content: space-between;}
.write li:nth-of-type(1) div{ width: 48%;}  /* name */
.write li:last-child{text-align: end;}

.write li label{ 
  display: block; color: rgb(197, 197, 197); 
  font-size: 15px; margin-bottom: 5px; 
}

.write input{
  width: 100%; border: none;
  background-color: rgba(255, 255, 255, 0.2);
  color: white; padding: 3px 10px;
}
.write textarea{ 
  width: 100%; height: 140px;  background-color: rgba(255, 255, 255, 0.3);
  resize: none; color: white; padding: 3px 10px;
}
textarea:focus, input:focus{
  outline: none;
}

.write button{ 
  background-color: rgba(255, 255, 255, 0.3);
  width: 30%; color: black; border: none;
  font-size: 13px; font-weight: 600; 
  font-style: normal; letter-spacing: .14em;
  padding: 5px 0;
}

.write li:nth-of-type(1) div span{ color: #aaa; font-size: 12px;}




/* 오른쪽 */
.right{ width: 30%;}
.right > p{
  color: white; font-size: 13px; font-weight: 500; 
  font-style: normal; letter-spacing: .14em;
  padding: 50px 0;
}
.location{
  width: 100%; height: 400px; background: rgba(255, 255, 255, 0.3);
}







/* 
  ===========================  Footer.js   ==================================== 
  =============================================================================
*/

footer{
  background: url('./img/bg5.jpg') no-repeat;
  width: 100%; background-size: cover; background-position-x: 50%;
  text-align: center;
  padding: 30px 0;
}
footer div a{ width: 20px; height: 20px; margin: 0 10px; opacity: 0.4;}
footer div a:hover{ opacity: 0.7;}
footer div a img{ width: 20px; height: 20px;}

footer ul{ margin: 10px 0;}
footer ul li{ color: #aaa; font-size: 13px; font-weight: 500; font-style: normal;
  letter-spacing: .14em;}
footer ul li a{ color: #aaa; font-size: 13px; font-weight: 400; margin: 0 7px;}
footer ul li a:hover{ color: #ccc;}
footer small {color: rgb(112, 112, 112); font-size: 12px; font-weight: 400; }